@import url(https://fonts.googleapis.com/css2?family=Aldrich&family=Comfortaa:wght@300;700&family=Dancing+Script&family=Roboto:wght@100&display=swap);
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

body {
  background-image: url(/Portfolio-React/static/media/Background-site-no-logo.1ecb71ef.jpg);
  cursor: url(/Portfolio-React/static/media/customArrow.e9b2593f.svg), auto;
  background-size: 1920px 1024px;
}

ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  overflow: hidden;
  background-color: #bfe7f1;
  width: 100%;
  justify-content: center;
}

li {
  font-family: "Aldrich";
  margin: 10px;
  font-size: 40px;
  float: left;
}

li a:hover {
  font-size: 37px;
  cursor: url(/Portfolio-React/static/media/darkCustomArrow.bb43ac29.svg), auto;
  color: #28738C;
}

li a {
  text-decoration: none;
  color: black;
}

.socials {
  font-size: 55px;
  float: right;
  margin: 6px;
  padding: 0;
  text-decoration: none;
  color: black;
  cursor: url(/Portfolio-React/static/media/darkCustomArrow.bb43ac29.svg), auto;
}

.socials:hover {
  color: white;
}

.infoCard  {
  font-family: "Aldrich";
  color: white;
  font-size: 20px;
  word-break: break-all;
}

.infoCard .hi {
  float: left;
  font-size: 40px;
  -webkit-animation-name: fading;
          animation-name: fading;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
}

.infoCard .iBuild {
  margin-left: -800px;
  margin-top: 100px;
  font-size: 30px;
  -webkit-animation-name: fading2;
          animation-name: fading2;
  -webkit-animation-duration: 5s;
          animation-duration: 5s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
}

@-webkit-keyframes fading{
  0%{opacity:0%;}
  100%{opacity: 100%;}
}

@keyframes fading{
  0%{opacity:0%;}
  100%{opacity: 100%;}
}
@-webkit-keyframes fading2{
  0%{opacity:0%;}
  100%{opacity: 100%;}
}
@keyframes fading2{
  0%{opacity:0%;}
  100%{opacity: 100%;}
}


.card {
  display: grid;
  grid-gap: 1px;
  gap: 1px;
  grid-template-columns: repeat(2, 1fr);
  margin-top: 80px;
}


.card img{
  border: 10px solid rgba(255, 255, 255, 0.06);
  height: 400px;
  width: 600px;
  opacity: 0.4;
}

.card img:hover{
  border: 10px solid #bfe7f1;
  opacity: 1;
}
.defaultLogo {
  display: block;
  padding-left: 39px;
  padding-top: 66px;
}

.defaultLogo:hover {
  -webkit-animation: spin 1s linear infinite;
  animation: spin 1s linear infinite;
  padding-left: 39px;
  padding-right: 39px;
}
@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotateY(360deg);
  }
}
@keyframes spin {
  100% {
    -webkit-transform: rotateY(360deg);
    transform: rotateY(360deg);
  }
}

.myName{
  font-family: "Aldrich";
  color: #6eb8c7;
  text-align: left;
  padding: 10px;
  margin-left: 215px;
  margin-top: -140px;
  margin-bottom: 90px;
  font-size: 65px;
}
.about{
    font-family: "Aldrich";
    color: white;
    font-size: 20px;
    font-size: 30px;
    line-height: 50px;
    /* opacity: .5; */
}

.about:hover{
